import noData from '@/components/noData'
import back from '@/components/back'
export default {
    components:{
        noData,
        back,
    },
    data(){
        return{
			user: this.$store.state.userInfo,
			Patient: this.$store.state.patient,
			page: 1,
			haveMore: true,
			search:'',
			list:[],
			Specialist:[],
			SubjectBuyRecordId:0,
			SpecialistHosGroupId:0,
			HosGroupName:'',
			num:0,
			SpeciaFlag:false,
			totalNum:0,
			DrIdList:[],
			totalMoney:0,
        }
    },
	 computed:{

	},
    created(){
		this.$bridge.callhandler('getSubjectBuyRecordId','',(data) => {
			this.SubjectBuyRecordId = data
		    responseCallback('接收到方法')
		})

		this.$post('MDTAPI/MDT/QuerySpecialistHosGroupPage',{
			PageIndex:this.page,
			PageSize:100,
			DrId:this.user.Data.User.DoctorId,
			HosGroupName:''
		}).then(o => {
			this.Specialist = o.ReturnData
			this.SpecialistHosGroupId = o.ReturnData[0].SpecialistHosGroupId
			console.log(this.SpecialistHosGroupId)
			this.HosGroupName = o.ReturnData[0].HosGroupName
			this.getMallMerchantInfoPage(false,o.ReturnData[0].SpecialistHosGroupId)
		})

	},
    mounted(){
		this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
		totalM(){
			this.totalNum = 0
			this.DrIdList = []
			if(this.list && this.list.length){
			    this.list.forEach(v => {
					if(v.state){
						this.totalNum += 1
						this.DrIdList.push(v.DrId)
					}
			    })
			}
            if(this.DrIdList && this.DrIdList.length){
                this.$post('MDTAPI/MDT/QueryMDTGroupPrice',{
                	SpecialistHosGroupId:this.SpecialistHosGroupId,
                	DrIdList:this.DrIdList,
                }).then(o => {
                	this.totalMoney = o
                })
            }
		},
		ClickRecord(){
			if(this.totalNum < 2){
				this.$toast("会诊团队不得低于2人")
				return
			}
			this.$post('MDTAPI/MDT/SaveMDTGroup',{
				SubjectBuyRecordId:this.SubjectBuyRecordId,
				SpecialistHosGroupId:this.SpecialistHosGroupId,
				HosGroupName:this.HosGroupName,
				DrIdList:this.DrIdList,
				PatientId: this.Patient.PatientId,
				PatientName: this.Patient.PatientName,
				SourceDrId:this.user.Data.User.DoctorId,
				SourceDrName:this.user.Data.User.DoctorName,
			}).then(o => {
				if(o){
				    const data = '123'
				    this.$bridge.callhandler('Back', data)
				}
			})
		},
		cancelSpecialist(){
			this.SpeciaFlag = false
		},
		openSpecialist(){
			this.SpeciaFlag = true
		},
		getMallMerchantInfoPage(h,SpecialistHosGroupId){
			if(this.haveMore){
				this.$post('MDTAPI/MDT/QuerySpecialistHosMemberPage',{
					PageIndex:this.page,
					PageSize:15,
					SpecialistHosGroupId,
					SubjectType:3, //诊疗类型(2-分诊，3-会诊)
					DrName:this.search,
				}).then(o => {
					if (!o.ReturnData || o.ReturnData.length < 15){
						this.haveMore = false
					}
					else {
						this.haveMore = true
					}
					if (h){
						o.ReturnData.forEach(v => {
							this.list.push(v)
						})
					}
					else {
						this.list = o.ReturnData
					}
					this.list.forEach((v,k) => {
						if(v.DrId === this.user.Data.User.DoctorId){
							this.list[k].state = 1
						}else{
							this.list[k].state = 0
						}
					})
					this.totalM()
				})
			}
		},
		searchFor(){
			this.page = 1
			this.haveMore = true
			this.getMallMerchantInfoPage(false,this.SpecialistHosGroupId)
		},
		clickspecialist(item,idx){
			this.num = idx
			this.page = 1
			this.haveMore = true
            this.SpecialistHosGroupId = item.SpecialistHosGroupId
            this.HosGroupName = item.HosGroupName
			this.getMallMerchantInfoPage(false,item.SpecialistHosGroupId)
			this.SpeciaFlag = false
		},
		TrigeBtn(i,idx){
			if(this.user.Data.User.DoctorId === i.DrId){
				return
			}
			this.list.forEach((v,k) => {
				if(v.DrId === i.DrId){
					if(i.state){
						this.list[k].state = 0
					}else{
						this.list[k].state = 1
					}
				}
			})
			this.SpeciaFlag = true
			this.SpeciaFlag = false
			this.totalM()
		},
        getMore(){
			const el = this.$refs.el
			console.log(el)
			let timer2 = null
			window.onscroll = () => {
				clearTimeout(timer2)
				timer2 = setTimeout(() => {
					const top = document.documentElement.scrollTop ||
							window.pageYOffset ||
							document.body.scrollTop,
							h = el.scrollHeight,
							wh = window.innerHeight
	
					if(h - top - wh < 100 && this.haveMore) {
						this.page += 1
						this.getMallMerchantInfoPage(true,this.SpecialistHosGroupId)
					}
				}, 100)
			}
        },
    }
}



